var calendar = require('./lib/calendar');

var finder = require('./lib/finder');

var translator = require('./lib/translator');

var FinnishHolidays = {
  next: function next(count, includeWeekends) {
    return calendar.next(count, includeWeekends);
  },
  year: function year(_year, includeWeekends) {
    return calendar.byYear(_year, includeWeekends);
  },
  month: function month(_month, year, includeWeekends) {
    return calendar.byMonth(_month, year, includeWeekends);
  },
  translate: function translate(english, language) {
    return translator.translate(english, language);
  },
  after: function after(month, year, index) {
    return finder.after(month, year, index);
  },
  before: function before(month, year, index) {
    return finder.before(month, year, index);
  }
};
module.exports = FinnishHolidays;